import { useState } from "react";
import "./styles.css";

var monkeyEmoji = {
  "üôà": "See-No-Evil Monkey",
  "üôâ": "Hear-No-Evil Monkey",
  "üôä": "Speak-No-Evil Monkey",
  "üêµ": "Monkey Face",
  "üêí": "Monkey",
  "ü¶ç": "Gorilla",
  "ü¶ß": "Orangutan",
  "üêó": "Boar",
  "üê®": "Koala",
  "üêº": "Panda"
};

let emojisWeKnow = Object.keys(monkeyEmoji);

export default function App() {
  const [emoji, setEmoji] = useState("");

  function inputHandler(event) {
    let currentEmoji = event.target.value;
    let searchedEmoji = monkeyEmoji[currentEmoji];
    if (searchedEmoji === undefined) {
      searchedEmoji = "emoji not found";
    }
    setEmoji(searchedEmoji);
  }
  function clickHandler(emoji) {
    let emojiName = monkeyEmoji[emoji];
    setEmoji(emojiName);
  }
  return (
    <div className="App">
      <div className="nav">
        <h1>Monkeypedia</h1>
      </div>
      <div className="main-section">
        <input className="maina" onChange={inputHandler}></input>
        <h2>{emoji}</h2>
        <h3>Our Database of Emojis</h3>
        <div className="emoji-animal">
          {emojisWeKnow.map((emoji) => (
            <span
              className="single-emoji"
              onClick={() => clickHandler(emoji)}
              key={emoji}
            >
              {emoji}
            </span>
          ))}
        </div>
      </div>
      <div className="footer">
        <h2>About</h2>
        <p>
          {" "}
          About this App:- You can click on any emoji to find out the meaning of
          the same.
        </p>
      </div>
    </div>
  );
}
